<template>
  <div class="about">
    <h1>{{id ? '编辑' :'新建'}}物品</h1>
    <el-form label-width='120px' @submit.native.prevent="save">
      <!-- <el-form-item label='上级分类'>
        <el-select v-model="model.parent">
          <el-option v-for="item in parentOptions" :key="item._id"
          :label="item.name" :value="item._id">

          </el-option>
        </el-select>
      </el-form-item> -->
      <el-form-item label='名称'>
        <el-input v-model="model.name"></el-input>
      </el-form-item>
      <el-form-item label='图标'>
        <el-upload class="icon-uploader" 
                   :action="uploadUrl"
                   :headers="getAuthHeaders()"
                   :show-file-list="false"
                   :on-success="afterUpload">
          <img v-if="model.icon" :src="model.icon" class="icon">
          <i v-else class="el-icon-plus icon-uploader-icon"></i>
        </el-upload>
      </el-form-item>
      <el-form-item>
        <el-button type='primary' native-type='submit'>保存</el-button>
      </el-form-item>
    </el-form>
  </div>
</template>

<script>
  export default {
    props: {
      id: {}
    },
    data() {
      return {
        model: {},
      }
    },
    methods: {
      afterUpload(res){
        this.$set(this.model,'icon',res.url)
        // this.model.icon = res.url
      },
      async save() {
        if (this.id) {
          await this.$http.put(`rest/items/${this.id}`, this
            .model) //之前定义res =   但报错 删除res
        } else {
          await this.$http.post('rest/items', this.model) // eslint-disable-line no-unused-vars
        }
        this.$router.push('/items/list')
        this.$message({
          type: 'success',
          message: '保存成功'
        })
      },
      async fetch() {
        const res = await this.$http.get(`rest/items/${this.id}`)
        this.model = res.data
      }
    },
    created() {
      this.id && this.fetch()
    }
  }
</script>

